  left: 100%;
}

.menu-btn > span,
.menu-btn > span::before,
.menu-btn > span::after {
  content: "";
  display: block;
  height: 3px;
  width: 25px;
  border-radius: 3px;
  background-color: #ffffff;
  position: absolute;
  transition: background-color 0.2s ease-in-out 0s, -webkit-transform 0.2s ease-in-out 0s;
  transition: transform 0.2s ease-in-out 0s, background-color 0.2s ease-in-out 0s;
  transition: transform 0.2s ease-in-out 0s, background-color 0.2s ease-in-out 0s, -webkit-transform 0.2s ease-in-out 0s;
}

.menu-btn > span::before {
  bottom: 8px;
}

.menu-btn > span::after {
  top: 8px;
}

#menu-btn-check {
  display: none;
}

#menu-btn-check:checked ~ .menu-btn span {
  background-color: rgba(255, 255, 255, 0);
}

